import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import regest from '@/components/regest'

//子路由
import loginname from "../components/user/loginename.vue"
import loginmail from "../components/user/loginmail.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login,
      // 配置子路由：
      children:[
        
        {
          path:'loginname',
          component:loginname
        },
        {
          path:'loginmail',
          component:loginmail
        }
      ]
    },
    {
      path: '/regest',
      name: 'regest',
      component: regest
    }
  ]
})



