<template>
    <div>
        
            
                <label for="userName">用户名</label>
                <input type="text" name="userName" :id="userName" v-model="userName">
                <label for="userPassword">密码</label>
                <input type="password" name="userPassword" :id="userPassword" v-model="userPassword">
            
            <button @click="login">用户名登录</button>
        
    </div>
</template>
<script>
import {ajax} from '../../network/request'
import qs from 'qs'

export default {
    data() {
        return {
            userName:'',
            userPassword:''
        }
    },
    methods: {
        login(){
            let data ={
                        userName:this.userName,
                        userPassword:this.userPassword
                    }
            ajax({
                url:'/user/login',
                method:'post',
                params:{},
                data:qs.stringify(data)
            })
            .then(response =>{
                let res = JSON.parse(response)
                if(res.status==0){
                    alert("token为"+res.data)
                }
                
                })
            .catch(err =>{
                alert("登录失败")
            })
        }
    },
}
</script>
<style scoped>
    
</style>


